body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Title {
  text-align: center;
  padding: 28vmin;
  font-size: 10vmin;
  pointer-events: none;
}

.Navigation-bar {
  display: flex;
  justify-content: center;
  font-size: 1em;
  border-right: 1px
}

.navlink {
  margin-left: 1em;
  margin-right: 1em;
  padding-left: 2em;
  padding-right: 2em;
}

.searchContainer {
  display: flex;
  justify-content: center;
}

.searchBar {
  height: 2em;
}



.ViewContainer {
  display: flex;
  margin-left: 5vw;
  margin-right: 5vw;

}

.filterHeaders {
  display:flex;
  
}

.timeRadio {
  margin-right: 5em;
}


.ChangeView {
  padding-top: 4px;
  padding-bottom: 4px;
  width: 12em;
  margin-left: 6em;
  margin-top: 3em;

}



td {
  text-align: center;
  width: 30em;
}

tr {
  height: 2em;
}


